import React, { useState } from "react";
import Page from "../component/Page";
import { useNavigate, useRoutes } from "react-router-dom";
import CryptoJS from "crypto-js";

function Checkout() {
  const [orderId, setOrderid] = useState(
    Math.floor(100000 + Math.random() * 900000)
  );
  const [refrenceId, setRefrenceId] = useState(
    Math.floor(100000 + Math.random() * 900000)
  );
  const [amount, setAmount] = useState(
    Math.floor(100000 + Math.random() * 900000)
  );

  const [customerName, setcustomerName] = useState(
    'rashmi lodhi'+Math.floor(100 + Math.random() * 900)
  );

  const [customerEmail, setcustomerEmail] = useState(
    'rashmilodhi'+Math.floor(100 + Math.random() * 900)+'@gmail.com'
  );
  const [customerPhone, setcustomerPhone] = useState(
    '+919669646'+Math.floor(100 + Math.random() * 900)
  );
  const secretPass = process.env.REACT_APP_SCERET_KEY;
  const handleCheckout = () => {
    const payload = [
      {
        api_key: process.env.REACT_APP_REACT_APP_API_KEY,
        merchant_id: process.env.REACT_APP_MERCHANT_ID,
        orderId: orderId,
        refrenceId: refrenceId,
        amount: amount,
        customerId: Math.floor(100000 + Math.random() * 900000),
        customerName: customerName,
        customerEmail: customerEmail,
        customerPhone: customerPhone,
        currency: "INR",
      },
    ];

    const data = CryptoJS.AES.encrypt(
      JSON.stringify(payload),
      secretPass
    ).toString();

    const final = data.replace(/[+]/g, "abcdefg");
    const timeout = setTimeout(() => {
      // 👇️ redirects to an external URL
      window.location.href =
        "http://65.2.151.133/#/impetrosys/data?sending_data=" + final;
    }, 3000);

    return () => clearTimeout(timeout);
  };
  return (
    <Page title="Checkout Page">
      <div className="webooks-new mt-5">
        <div className="container">
          <div className="row">
            <div className="mx-auto my-auto col-12 col-md-8">
              <div className="webhook-one">
                <div className="card">
                  <div className="card-header">
                    <h6 className="mb-3 mt-3 paymethods-1">
                      Check Out Details
                    </h6>
                  </div>
                  <div className="card-body">
                    <form>
                      <div className="row">
                        <div className="col-lg-6 col-md-6 col-sm-12">
                          <div className="form-group">
                            <label className="control-label">
                              Order Refrence Id
                            </label>
                            <input
                              className="form-control"
                              type="text"
                              name="RefrenceId"
                              disabled
                              defaultValue={refrenceId}
                            />
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12">
                          <div className="form-group">
                            <label className="control-label">Customer Id</label>
                            <input
                              className="form-control"
                              type="text"
                              name="CustomerId"
                              disabled
                              defaultValue={Math.floor(
                                100000 + Math.random() * 900000
                              )}
                            />
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12">
                          <div className="form-group">
                            <label className="control-label">Customer Name</label>
                            <input
                              className="form-control"
                              type="text"
                              name="CustomerName"
                              disabled
                              defaultValue={customerName}
                            />
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12">
                          <div className="form-group">
                            <label className="control-label">Customer Email</label>
                            <input
                              className="form-control"
                              type="email"
                              name="CustomerEmail"
                              disabled
                              defaultValue={customerEmail}
                            />
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12">
                          <div className="form-group">
                            <label className="control-label">Customer Phone</label>
                            <input
                              className="form-control"
                              type="text"
                              name="CustomerPhone"
                              disabled
                              defaultValue={customerPhone}
                            />
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12">
                          <div className="form-group">
                            <label className="control-label">Order Value</label>
                            <input
                              className="form-control"
                              type="text"
                              name="OrderValue"
                              disabled
                              defaultValue={amount}
                            />
                          </div>
                        </div>
                        <div className="col-lg-12 col-sm-12 mb-4 mt-3">
                          <button
                            type="button"
                            className="w-30"
                            onClick={handleCheckout}
                          >
                            checkout
                          </button>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
}

export default Checkout;



